* {
  margin: 0;
  padding: 0;
  list-style: none;
}
.prenavbar {
  background-color: #212121;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding-left: 5vmax;
  padding-right: 5vmax;
  padding-top: 10px;
  height: 50px;
}
.prenavbar > ul {
  display: flex;
  justify-content: center;
  align-items: center;
}
.prenavbar > :nth-child(2) > :last-child > a > i > svg {
  width: 20px;
}
.prenavbarNavLinks {
  color: #b0b0b0;
  text-decoration: none;
  text-transform: uppercase;
  margin-left: 10px;
  margin-right: 10px;
  font-size: 12px;
}
.prenavbarNavLinks:hover {
  color: rgb(197, 197, 197, 197);
  text-decoration: none;
}
.horizontalBar {
  color: #b0b0b0;
}
